export const initMobApp = async (): Promise<void> => {
  const { AdMob } = await import('@capacitor/admob');
  await AdMob.initialize();
};

export const showBannerAd = async (): Promise<void> => {
  const { AdMob, BannerAdSize } = await import('@capacitor/admob');
  await AdMob.showBanner({
    adId: process.env.VITE_MOBAPP_BANNER_ID!,
    adSize: BannerAdSize.ADAPTIVE_BANNER,
    position: 'BOTTOM_CENTER',
  });
};

export const hideBannerAd = async (): Promise<void> => {
  const { AdMob } = await import('@capacitor/admob');
  await AdMob.hideBanner();
};
